@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,200;1,281;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,200;1,281;1,300&display=swap);
.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-family: 'Raleway';
  
  
}
body {
  padding: 0;
  margin: 0;
 
  
}
img{
  padding-top: 100px;
}

.profile-page {
    width: 100%;
    height: 100%;
    font-family: 'Raleway';
    background-image: url(/FIFA-Search-Browser/static/media/profilebackground.61da95d8.png);
    background-repeat: no-repeat;
    background-size:100% 100%;
    
}
.flex-container {
    width:  100%;
    min-width: 100%;
    display: flex;
    flex-direction: row;
}

.flex-item {
    flex: 1 1;
    padding: 20px;
    
    
}

.col-2 {
    justify-content: flex-end;
    border-left: solid rgb(202, 202, 202) 2px;
    padding-left: 95px;
    padding-top: 44px;
    
}
.progress-box{
    text-align: center;
    text-align: left;
    text-indent: 150px;
    padding-right: 70px;
    padding-left: 50px;
    display: flex;
    flex-direction: column-reverse;
    font-size: 19px;
}


.border-sp {
    background-color: rgb(66, 128, 66);
    color: white;
    width: 100%;
    padding: 12px 44px 12px 44px;
    border-radius: 10px;
    margin-right: 50px;
}

.over-div {
    margin-bottom: 24px;
    width: 100%;
    font-weight: bolder;
}

.header {
    text-align: center;
    position: relative;
    align-items: center;
    width: 100%;
    height: 70px;
    color: rgb(139, 139, 139);
    font-weight: bolder;
    margin-bottom: 40px;
}

.name-header {
    display: inline-block;
    font-size: 26px;
    margin-block: 0;
    margin-inline: 0;
    margin: 1em;
}

.btn, .btn {
    position: absolute;
    margin: 1em;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
    border: none;
    background-color: transparent;
    color: rgb(139, 139, 139);
}
.btn-home{
    left: 200px;
}
.btn-help{
    left: 300px;
}
progress{
    -webkit-appearance: none;
   appearance: none;
   width: 40%;
   height: 10px;
   float: right;

}
progress[value]::-webkit-progress-value {
    box-shadow: none;
    background-image: 
         -webkit-linear-gradient(left,  rgb(66, 128, 66),  rgb(66, 128, 66));
      border-radius: 22px;
  }
  progress[value]::-webkit-progress-bar {
    background-color: #eee;
    border-radius: 22px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
  }
strong{
    margin:5px  ;
}

.search-page {
    width: 100%;
    height: 100%;
    background-image: url(/FIFA-Search-Browser/static/media/searchbackground.236bded4.png);
    background-repeat: no-repeat;
    background-size:100% 100% ;
    text-align: center;
    font-weight: bold;
}

.search-container {
    display: inline-block;
    vertical-align: top;
    margin: 24px;
}

.search-box {
    margin: 24px;
}

.search-input {
    width: 390px;
    background-color:#ffffff;
    border: rgb(177, 177, 177) solid 0.5px;
    color: rgb(0, 0, 0);
    height: 40px;
    box-shadow: rgba(0, 0, 0, 0.1) -4px 9px 25px -6px;
    padding: 4px 24px 4px 24px;
    margin-bottom: 5px;
    border-radius: 10px;    
}

input:focus {
    outline: none;
    box-shadow: none;
}

.search-result {
    border-radius: 10px;
    border: rgb(177, 177, 177) solid 0.5px;
    color: rgb(0, 0, 0);
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 4px 12px;
}

.player-data-item {
    text-align: left;
    padding: 8px 10px 8px 10px;
    color: rgb(0, 0, 0);
    margin: 0px;
}

.player-data-item:hover {
    background-color: rgb(211, 211, 211);
    border-radius: 10px;
}

a {
    text-decoration: none;
    color: black;
}
